<template>
  <div class="storyparallax">
    <img src="@/assets/romeimage.jpg">
    <div class="ptxt">
      <span class="border">
        GEOPOGO
      </span>
    </div>
    <div class="parallax">
      <h1>INSPIRATION</h1>
      <h2>Geopogo is inspired by a map of Rome drawn by an architect and surveyor, Giambattista Nolli, in 1748. Nolli challenged the paradigm of drawing hard lines between the outside and inside worlds. In Nolli's map, outdoor public spaces flow seamlessly into the interiors of semi-public buildings such as the Pantheon. Though visionary at the time, it was entirely logical, given that this is how we all experience the real world.</h2>
      <h1>WHAT WE DO</h1>
      <h2>Geopogo modernizes Nolli's innovation by building 3D, immersive models of real places that bridge the outdoor and indoor worlds. We look ahead to the unlimited possibilities of virtual reality, the excitement of crossing from public into private worlds, and the blending of the past with the imagination of the future.</h2>
      <h1>MISSION</h1>
      <h2><b>Discovery </b>|Open doors and windows to the world</h2>
      <h2><b>Access </b>|Provide free movement to everyone</h2>
      <h2><b>Connection </b>|Offer new ways of connection</h2>
      <h2><b>Immersion </b>|Deliver a completely engaging experience</h2>
      <h1>VISION</h1>
      <h2 class="last">Geopogo aims to be the destination of choice for people to explore the world, conduct e-commerce, and connect with others in virtual space.</h2>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Story',
  data () {
    return {

    }
  },
}
</script>

<style scoped>
.storyparallax img{
  width: 100%;
  height: 100%;
  z-index: 0;
  position: relative;
}

.ptxt {
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
  color: #606060;
  font-size: 27px;
  letter-spacing: 8px;
  text-transform: uppercase;
}

.ptxt .border {
  background-color: #606060;
  color: white;
  padding: 10px;
}

.ptxt .border.trans {
  background-color: transparent;
}

.parallax{
  text-align: center;
}

h1{
  font-size: 5vh;
  letter-spacing: .3em;
  margin-top: 25vh;
}

h2{
  font-size: 2vh;
  padding-left: 20vw;
  padding-right: 20vw;
  letter-spacing: .1em;
}

.last{
  margin-bottom: 25vh;
}
</style>



